.adonitrans-plug #contloader {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  flex-direction: column;
  z-index: 9999;
  opacity: 0;
  transform: scale(0);
  transition: all 0.5s;
}
.adonitrans-plug #contloader .text {
  color: #fff;
}
.adonitrans-plug #contloader .loader {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: inline-block;
  border-top: 4px solid #FFF;
  border-right: 4px solid transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.adonitrans-plug #contloader .loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border-left: 4px solid #00d6b0;
  border-bottom: 4px solid transparent;
  animation: rotation 0.5s linear infinite reverse;
}
.adonitrans-plug.actloader #contloader {
  transform: scale(1);
  opacity: 1;
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.adonitrans-plug .formplug label {
  width: 100%;
